@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600;700&display=swap");
* {
  font-family: "Source Sans Pro", sans-serif;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #7cbef2;
  font-size: 16px;
}

input {
  font-size: 16px;
  margin: 0;
}

ul,
li {
  padding: 0;
  margin: 0;
}

.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.container {
  min-width: 60%;
  padding: 20px;
  text-align: left;
  background-color: #ffffff;
  border-radius: 18px;
}

.title {
  font-size: 26px;
  font-weight: 600;
  color: #2f80ed;
}

.subtitle {
  margin-top: 30px;
  font-weight: 600;
  font-size: 18px;
  color: #333333;
}

.list {
  position: relative;
  height: 200px;
  overflow: scroll;
  scroll-behavior: smooth;
}

.scroll {
  cursor: pointer;
  position: sticky;
  bottom: 0;
  left: 50%;
  color: #a6c5dc;
  background-color: #e1f2ff;
  transform: translateX(-50%);
  border: none;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  transition: all ease 0.2s;
}

.scroll:hover {
  color: #8db5d4;
  background-color: #c6e3fa;
}

.scroll:active {
  background-color: #e5f4ff;
}
